.fetchingDataSpinner {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 4px;
  right: 4px;
  top: 4px;
  width: calc(100% - 20px);
  height: 100%;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
}
